import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import { uniSwipeAction } from '@/components/uni-swipe-action/uni-swipe-action.vue';
const app = getApp();
export default {
	components: { uniSegmentedControl, uniSwipeAction },
	data() {
		return {
			isRefreshData: false,
			dataList: [],
			items: ['支出', '收入'],
			currentCategoryType: 0,
			_db: null
		};
	},
	onLoad(options) {
		const that = this;
		this._db = app.globalData.wxDB
		console.log(options)
		this.currentCategoryType = options.categoryType || 0
	},
	onShow() {
		console.log('app:', app);
		this.getCategoryList();
	},
	methods: {
		getCategoryList() {
			const type = this.currentCategoryType;
			wx.cloud
				.callFunction({
					name: 'getCategoryList',
					data: {
						type
					}
				})
				.then(({ result }) => {					
					console.log(result)
					this.dataList = result.data
				})
				.catch(console.error);
		},
		handleDeleteCategory() {
			uni.showModal({
				title: '提示',
				content: '确认要删除吗？',
				success: function(res) {
					if (res.confirm) {
						_this.deleteCategory();
					}
				}
			});
			// this._db.collection('Category').doc('');
		},
		handleTabsItem(index) {
			if (this.currentCategoryType !== index) {
				this.currentCategoryType = index;
				this.getCategoryList();
			}
		},
		handleAddCategory() {
			uni.navigateTo({
				url: '../icon-manage/index?categoryType=' + this.currentCategoryType
			});
		}
	}
};
